/*
 * Component: Button
 * -----------------
 */
.btn.btn-flat {
  border-radius: 0;
  box-shadow: none;
  border-width: 1px;
}

.btn.btn-file {
  position: relative;
  overflow: hidden;
}

.btn.btn-file > input[type='file'] {
  position: absolute;
  top: 0;
  right: 0;
  min-width: 100%;
  min-height: 100%;
  font-size: 100px;
  text-align: right;
  opacity: 0;
  outline: none;
  background: white;
  cursor: inherit;
  display: block;
}

.btn-default {
  background-color: #f4f4f4;
  color: #444;
  border-color: #ddd;
}

.btn-default:hover, .btn-default:active, .btn-default.hover {
  background-color: #e7e7e7;
}

