  padding: .5rem 1rem .5rem .5rem;
  border-left: 5px solid #eee;
}

.callout a {
  color: #ffffff;
  text-decoration: underline;
}

.callout a:hover {
  color: #eee;
}

.callout p:last-child {
  margin-bottom: 0;
}

.callout.callout-danger {
  border-left-color: #bd2130;
}

.callout.callout-warning {
